.source-sans-pro {
  font-family: "Source Sans Pro", sans-serif;
}

.merriweather {
  font-family: "Merriweather", sans-serif;
}

.open-sans {
  font-family: "Open Sans", sans-serif;
}

.montserrat {
  font-family: "Montserrat", sans-serif;
}

.lead-paragraph {
  font-family: "Merriweather", sans-serif;
  font-size: 20px;
  line-height: 34px;
  color: #205493;
}

.sfgov-banner {
  background: #e1f3f8;
}

.sfgov-banner .sfgov-banner__container {
  overflow: auto;
  padding: 42px 0 53px 0;
  display: flex;
  flex-direction: column;
}

.sfgov-banner h1 {
  color: #205493;
  font-family: "Merriweather", sans-serif;
}

.sfgov-banner .lead-paragraph {
  margin-bottom: 25px;
}

.sfgov-banner .date {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 15px;
  color: #5b616b;
  line-height: 19px;
}
